import React from 'react';
import { BrowserRouter as Router, Route, Redirect, Switch} from 'react-router-dom';
//import {BrowserHistory} from 'react-router';
import ReactDOM from 'react-dom';
import Header from './header'
import Main from './main';
import './main.css';
import { render } from '@testing-library/react';
import Footer from './footer';

interface IProps {
  train: boolean,
  sidebar?: boolean,
}
interface IState {
  sidebarHidden: boolean,
  gameStarted: boolean,
  train: boolean,
}
class App  extends React.Component<IProps, IState> {
  constructor (props:any) {
    super(props);
    this.state = {
      sidebarHidden: true,
      gameStarted: false,
      train: true,
    }
  }

  playSound(addr: string) {
    const audio = new Audio(process.env.PUBLIC_URL + '/' + addr);
    audio.currentTime = 0;
    audio.play();
  }
  startGame = () => {
    const buttonStart = document.querySelector('.main__button');
    buttonStart?.classList.add('main__button_push');
    setTimeout(() => {
      buttonStart?.classList.remove('main__button_push');
      }, 500);
    setTimeout(() => {
      this.setState({
        gameStarted: true,
      })
    }, 600)
  }
  setTrain = () => {
    this.setState((state) => ({
      train: !this.state.train,
    }));
  }
  hideSidebar = (e: any): void => {
    if (this.state.sidebarHidden === false 
      && this.state.gameStarted) {
        const links = document.querySelectorAll('.sidebar__link');
        links.forEach((item) => {
          if (e.target === item) {
            this.setState({
              gameStarted: false,
            });
          }
        })
    } 
    if (e.target !== document.querySelector('.sidebar')
      && e.target !== document.querySelector('.sidebar__list')
      && e.target !== document.querySelector('.sidebar__login')
      && this.state.sidebarHidden === false) {
        this.setState({
          sidebarHidden: !this.state.sidebarHidden,
        });
    } else if (e.target === document.querySelector('.menu-toggle')) {
      this.setState({
        sidebarHidden: !this.state.sidebarHidden,
      });
    }
  }
  render() {
    const train = this.state.train;
    if (train && this.state.gameStarted) {
      this.setState({
        gameStarted: false,
      })
    };

    return(
      <Router>
        <div className="app" onClick={this.hideSidebar}>
          <Route path="/" exact> 
            <div>
              <Header index="0" train={train} toggleSidebar={this.hideSidebar} sidebar={this.state.sidebarHidden} setTrain={this.setTrain}/>
              <Main train={train} index="0" startGame={this.startGame} gameStarted={this.state.gameStarted} setTrain={this.setTrain} />
              <Footer />
            </div>
          </Route>
          <Route path="/main/:id" render={({match}) => {
            const id = match.params.id;
            return (
              <div>
                <Header index={id} train={train} toggleSidebar={this.hideSidebar} sidebar={this.state.sidebarHidden} setTrain={this.setTrain}/>
                <Main index={id} train={train} startGame={this.startGame} gameStarted={this.state.gameStarted} setTrain={this.setTrain} />
                <Footer />
              </div>
            )
          }} />

        
          
          </div>
      </Router>

      )
    }
  }

export default App;
